@import "mixins";


.first-screen {
    width: 100%;
    min-height: 100vh;
    background-color: #fff;
    position: relative;
    padding: 154px 0 50px 0;
    @include tablet2 {
        padding: 120px 0 50px 0;
    }

    display: flex;
    justify-content: right;
    @media (max-width: 1630px) {
        display: block;
    
    }
    // .first-screen__container

    &__container {
        padding: 0 50px 0 15px; 
        max-width: 1630px;
        height: 100%;
        @media (max-width: 1630px) {
            padding: 0 15px 0 15px; 
        }
    }

    // .first-screen__body

    &__body {
        width: 100%;
        height: 100%;
        @include flex-all-sb;
        @media (max-width: 1630px) {
            flex-direction: column;
            align-items: flex-start;
        }
    }

    // .first-screen__content

    &__content {      
        font-family: $lucindaFont;
        z-index: 2;
        max-width: 600px;
        margin: 0 110px 0 0;
        @media (max-width: 1630px) {
            margin: 0 0 90px 0;
        }
        @include mobile {
            margin: 0 0 60px 0;
        }
    }

    // .first-screen__subtitle

    &__subtitle {
        color: $darkColor;
        position: relative;
        font-size: 30px;
        padding: 0 0 0 70px;
        margin: 0 0 70px 0;
        &::before {
            position: absolute;
            content: "";
            left: 0;
            top: 15px;
            width: 33px;
            height: 3px;
            background-color: $colorGrey;
            @include mobile {
                top: 10px;
            }
        }
        @media (max-width: 1630px) {
            margin: 0 0 20px 0;
        }
        @include mobile {
            font-size: 20px;
        }
    }

    // .first-screen__title

    &__title { 
        margin: 0 0 70px 0;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        @media (max-width: 1630px) {
            margin: 0 0 30px 0;
        }
      
        div {
            &:last-child {
                color: $darkColor;
                text-align: center;
                letter-spacing: 0.05em;
                text-transform: uppercase;
                font-size: 80px;
                position: relative;
                @include mobile {
                    font-size: 35px;
                }
                &::before {
                    position: absolute;
                    content: "";
                    left: -30px;
                    top: 10px;
                    display: block;
                    width: 99px;
                    height: 99px;
                    background-image: url("../img/first-screen/cube.png");
                    @media (max-width: 1630px) {
                        display: none;
                    }
                }
            }   
        }
    }

    // .first-screen__titlebold

    &__titlebold {
        color: $colorRed;
        letter-spacing: 0.05em;
        text-transform: uppercase;
        font-size: 130px;
        @include mobile {
            font-size: 60px;
        }
    }

    // .first-screen__text

    &__text {
        margin: 0 0 150px 0;
        color: $colorGrey;
        width: 100%;
        font-size: 15px;
        line-height: 140%;
        @media (max-width: 1630px) {
            margin: 0 0 50px 0;
        }
        @include mobile {
            font-size: 13px;
            margin: 0 0 20px 0;
        }
    }

    // .first-screen__row

    &__row {
        width: 100%;
        @include flex-all-sb;
        margin: 0 0 50px 0;
        @media (max-width: 1630px) {
            margin: 0 0 20px 0;
        }
    }

    // .first-screen__arrow

    &__arrow {
        width: 87px;
        height: 87px;
       position: relative;
       animation: arrow-move 3s infinite ease;
       @include mobile {
        width: 47px;
        height: 47px;
        animation: arrow-move-mobile 3s infinite ease;
        }
        @media (max-width: 350px) {
            display: none;
        }
    }
    @keyframes arrow-move  {
        0% {
            transform: translateX(-30px);
            
        }
        25% {
            transform: translateY(-30px);
        }
        50% {
            transform: translateX(30px);
        } 
        100% {
            transform: translateY(30px);
        }
    }
    @keyframes arrow-move-mobile  {
        0% {
            transform: translateX(-15px);
            
        }
        25% {
            transform: translateY(-15px);
        }
        50% {
            transform: translateX(15px);
        } 
        100% {
            transform: translateY(15px);
        }
    }

    // .first-screen__button

    &__button {
        width: 323px;
        @include mobile {
            width: 250px;
        }
    }

    // .first-screen__description

    &__description {
        color: $darkColor;
        width: 100%;
        font-size: 20px;
        line-height: 140%;
    }

    // .first-screen__slider

    &__slider {
        max-width: 825px;
        @media (max-width: 1630px) {
            max-width: 100%;
        }
    }

    // .first-screen__illustration

    &__illustration {
        position: absolute;
        bottom: 0;
        left: 0;
        margin: 0 0 0 20px;
        @media (max-width: 1750px) {
            display: none;
        }
    }

    // .first-screen__illustration2

	&__illustration2 {
        position: absolute;
        bottom: -30px;
        left: -90px;
        @media (max-width: 1750px) {
            display: none;
        }
	}

    // .first-screen__sliderwrap

	&__sliderwrap {
        position: relative;
        width: 100%;
	}

}


// Slider
.first-screen-slider {
    border-radius: 10px;
    width: 100%;
    // .first-screen-slider__item

    &__item {
        width: 100%;
        height: 100%;
        position: relative;
        padding: 40px 0 50px 0;
        border-radius: 10px;
        height: 820px;
        @include mobile {
           height: 600px;
           padding: 20px 0 30px 0;
        }
    }

    // .first-screen-slider__background

    &__background {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-size: cover;
        background-position: center;
        z-index: -10;
        background-image: url("../img/first-screen/image1.png");
        border-radius: 10px;
    }

    // .first-screen-slider__top

    &__top {
        margin: 0 0 290px 0;
        span {
            letter-spacing: 0.05em;
            text-transform: uppercase;
            font-size: 25px;
            font-weight: 700;
            color: #fff;
            font-family: $heliosFont;
            padding-left: 50px;
            line-height: 35px;
            @include mobile {
                font-size: 15px;
                margin: 0 0 90px 0;
            }
            @include mobile2 {
                padding-left: 10px;
            }
        }
        width: 500px;
        border-bottom: 3px solid #FEFEFE;
        border-top: 3px solid #FEFEFE;
       
        @include mobile {
            width: 400px;
        }
        @include mobile2 {
            width: 100%;
        }
     
    }

    // .first-screen-slider__bottom

    &__bottom {
        width: 100%;
        position: relative;
        transition: .3s all ease 0s;
    }

    // .first-screen-slider__shadow

    &__shadow {
        position: absolute;
        bottom: -50px;
        z-index: -1;
        width: 100%;
    }

    // .first-screen-slider__arrows

    &__arrows {
        width: 100%;
        margin: 0 0 108px 0;
        padding: 0 46px;
        @include flex-all-sb;
        @include mobile {
            padding: 0 16px;
            margin: 0 0 50px 0;
        }
    }

    // .first-screen-slider__texts

    &__texts {
        width: 100%;
        transition: .4s all ease 0s;
        color: #fff;
        margin: 0 0 30px 0;
        padding: 0 55px;
        transition: .3s all ease 0s;
        @include mobile {
            padding: 0 15px;
            margin: 0 0 15px 0;
         }
    }

    // .first-screen-slider__title

    &__title {
        font-family: $heliosFont;
        letter-spacing: 0.05em;
        text-transform: uppercase;
        font-size: 30px;
        margin: 0 0 30px 0;
        @include mobile {
            font-size: 25px;
            margin: 0 0 10px 0;
         }
    }

    // .first-screen-slider__text

    &__text {
        font-family: $lucindaFont;
        max-width: 575px;
        font-size: 20px;
        line-height: 140%;
        @include mobile {
            font-size: 14px;
            line-height: 110%;
         }
    }

    // .first-screen-slider__button

    &__button {
        width: 322px;
        display: none;
        visibility: hidden;
       
    }

    // .first-screen-slider__more

    &__more {
        width: 100%;
        @include flex-all-sb;
        border-bottom: 3px solid #FEFEFE;
        border-top: 3px solid #FEFEFE;
        transition: .3s all ease 0s;
        @media (max-width: 400px) {
            display: none;
        }
        a {
            font-family: $heliosFont;
            color: #fff;
            margin: 0 0 0 50px;
            align-items: center;
            letter-spacing: 0.05em;
            text-transform: uppercase;
            font-weight: 700;
            font-size: 20px;
            @include mobile {
                font-size: 14px;
                margin: 0 0 0 15px;
            }
        }
    }

    // .first-screen-slider__down

    &__down {
        margin: 0 180px 0;
        width: 22px;
        height: 28px;
        padding: 5px 0;
        @include mobile {
            margin: 0 40px 0;
        }
    }
}


.first-screen-slider__title:hover {
    .first-screen-slider__bottom {
        @include flex-all-center;
        flex-direction: column;
        text-align: center;
        margin: 0 0 58px 0;
    }
    .first-screen-slider__texts {
        @include flex-all-center;
        flex-direction: column;
        text-align: center;
    }
    .first-screen-slider__more { 
        transform: translateY(1000%);
    }
    .first-screen-slider-arrow-left {
        transform: translateX(-1000%);
    }
    .first-screen-slider-arrow-right {
        transform: translateX(1000%);                
    }
    .first-screen-slider__button {
        visibility: visible;
        display: block;
        transition: .3s all ease 0s;
    }
}

.first-screen-slider-arrow-left,
.first-screen-slider-arrow-right {
    cursor: pointer;
    transition: .3s all ease 0s;
    position: relative;
    @include mobile {
       width: 45px;
       height: 45px;
    }
}
