@import "mixins";


.footer {
    width: 100%;
    height: 104px;
    position: relative;
    border-top: 3px solid #625C5C;
    background-color: #fff;
    @include tablet {
       padding: 10px 0;
       height: auto;
    }
    &::before {
        position: absolute;
        content: "";
        background-color: $colorBlue;
         right: 0;
         top: 0;
         width: 60%;
         height: 100%;
         @media (max-width: 1700px) {
             width: 58%;
         }
         @media (max-width: 1100px) {
            width: 60%;
        }
         @include tablet {
             width: 100%;
         }
    }
   
   

    &__container {
        margin: 0 auto;
        padding: 0 15px;
        max-width: $totalContainer;
        height: 100%;
    }

    // .footer__body

    &__body {
        width: 100%;
        height: 100%;
        @include flex-all-sb;
        font-family: $lucindaFont;
        @include tablet {
            flex-direction: column;
            align-items: center;
        }
    }

    // .footer__main

    &__main {
        @include flex-v-center;
        @include tablet {
          margin: 0 0 20px 0;
         }
         @include mobile {
            flex-direction: column;
        }
    }

    // .footer__logo

    &__logo {
        margin: 0 20px 0 0;
        position: relative;
        z-index: 3;
        @include mobile {
            margin: 0 0 20px 0;
        }
    }

    // .footer__block

    &__block {
        position: relative;
        z-index: 3;
        @include mobile {
            text-align: center;
        }
    }

    // .footer__text

    &__text {
       color: $colorGrey; 
       font-size: 15px;
    }

    // .footer__confidency

    &__confidency {
        transition: .2s all linear 0s;
        padding-bottom: 1px;
        &:hover {
            border-bottom: 1px solid $colorGrey;
        }
       
    }   

    // .footer__copyright

    &__copyright {
        margin: 10px 0 0 0;
       
    }

    // .footer__contacts

    &__contacts {
        @include flex-v-center;
        height: 100%;
        @include mobile {
            flex-direction: column;
        }
    }

    // .footer__consultation

    &__consultation {
        position: relative;
        width: 343px;
        height: 100%;
        right: -20px;
        @include flex-all-center;
        @include tablet {
            width: auto;
            margin: 0 30px 0 0;
            right: 0;
        }
        @include mobile {
            margin: 0 0 10px 0;
        }
    }

    // .footer__consultationtext

    &__consultationtext {
        color: #fff;
        text-align: center;
        letter-spacing: 0.05em;
        text-transform: uppercase;
        font-weight: 700;
        position: relative;
        z-index: 2;
        top: 5px;
        font-family: $heliosFont ;
    }

    // .footer__consultationbg

    &__consultationbg {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        @include tablet {
            display: none;
        }
        
    }

    // .footer__number

    &__number {
        width: 288px;
        height: 100%;
        position: relative;
        z-index: 3;
        @include tablet {
            width: auto;
        }
    }
}



