.phone-number {
    @include flex-all-center;
    // .header-number__background

    &__background {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        
        @include tablet {
            display: none;
        }
    }

    // .header-number__content

    &__content {
        position: relative;
        z-index: 2;
       display: flex;
       align-items: center;
    }

    // .header-number__ico

    &__ico {
        width: 25px;
        height: 25px;
        margin: 0 8px 0 0;
        @media (max-width: 350px) {
            display: none;
        }
    }

    // .header-number__text

    &__text {
        
        text-transform: uppercase;
        color: #fff;
        font-size: 25px;
        font-weight: 700;
        font-family: $heliosFont;
        @include mobile2 {
            font-size: 20px;
        }
    }
}

